body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: orange;
  font-size: 18px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.map {
  border: solid 1px orange;
  margin-top: 10px;
  color: black;
  width: 70vw;
  height: 40vh;
}

.map > div:first-child {
  position: relative !important;
}

.content {
  margin: auto;
  width: 100%;
  height: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.placesList {
  z-index: 1;
  position: relative;
}

.placeListTitle:hover {
  color: gold !important;
  cursor: pointer;
}

.placeListTitle {
  color: orange;
  font-weight: bold;
}

.placeTitle:hover {
  cursor: pointer;
}

.placeListObject {
  margin-top: 5px;
  padding: 0 5px 5px 5px;
}

.linearGradient {
  background-image: -webkit-gradient(linear, left top, right top, from(#282828) , to(#000000));
  background-image: -webkit-linear-gradient(left, #282828 , #000000);
  background-image: linear-gradient(to right, #282828 , #000000);
}

.goldenButton {
  background-color: gold !important;
}
.createPlaceForm {
    margin-top: 10px;
    margin-bottom: 20px;
    z-index: 1;
    position: relative;
}

.updatePlaceForm {
    margin-top: 5px;
}

.createPlaceLabel, .updatePlaceLabel {
    float: left;
}
  
.createPlaceInput, .updatePlaceInput {
    display: block;
    margin-left: 150px;
    background-color: orange;
    border: none;
    border-radius: 5px;
    padding: 5px;
}

.createPlaceInput:hover, .updatePlaceInput:hover,
.createPlaceInput:focus, .updatePlaceInput:focus {
    background-color: gold;
}
  
.addPlaceButton,
.createPlaceSaveButton,
.deletePlaceButton,
.updatePlaceButton,
.updatePlaceSaveButton {
    background-color: orange;
    cursor: pointer;
    padding: 5px;
    border: none;
    border-radius: 3px;
    font-size: 18px;
    margin-top: 5px;
}

.addPlaceButton:hover,
.createPlaceSaveButton:hover,
.deletePlaceButton:hover,
.updatePlaceButton:hover,
.updatePlaceSaveButton:hover {
    background-color: gold;
}

.deletePlaceButton {
    margin-top: 5px;
    margin-right: 5px;
}

.updatePlaceButton {
    margin-top: 5px;
    margin-left: 5px;
}

.createPlaceSaveButton, .updatePlaceSaveButton {
    margin-top: 10px;
    margin-left: 150px;
}

.createPlaceFormRow, .updatePlaceFormRow {
    margin-top: 5px;
}
@media only screen and (max-width: 450px) {
    .createPlaceLabel, .updatePlaceLabel {
        float: none;
        width: 100%;
    }

    .createPlaceInput, .updatePlaceInput {
        margin-left: 0;
        width: 100%;
    }

    .createPlaceSaveButton, .updatePlaceSaveButton {
        margin-left: 0;
    }
}
